                        :checked="true"
                        ui="xs"
                        disabled
                    />
                    <veui-switch
                        :checked="false"
                        ui="xs"
                    />
                    <veui-switch
                        :checked="false"
                        ui="xs"
                        disabled
                    />
                </veui-field>
                <veui-field label=" [ui=s]">
                    <veui-switch v-model="value2"/>
                    <veui-switch
                        :checked="true"
                        disabled
                    />
                    <veui-switch
                        :checked="false"
                    />
                    <veui-switch
                        :checked="false"
                        disabled
                    />
                </veui-field>
                <veui-field label=" [ui=m]">
                    <veui-switch
                        ui="m"
                        v-model="value2"
                    />
                    <veui-switch
                        :checked="true"
                        ui="m"
                        disabled
                    />
                    <veui-switch
                        :checked="false"
                        ui="m"
                    />
                    <veui-switch
                        :checked="false"
                        ui="m"
                        disabled
                    />
                </veui-field>
            </veui-form>
        </section>

        <h2>[ui=m]</h2>
        <section>
            <veui-form>
                <veui-field label="">
                    <veui-switch
                        ui="m"
                        v-model="value2"
                    >
                        <template
                            slot="content"
                            slot-scope="{ on }"
                        >
                            {{on ? "" : ""}}
                        </template>
                    </veui-switch>
                </veui-field>
                <veui-field label="">
                    <veui-switch
                        ui="m"
                        v-model="value2"
                    >
                        <template
                            slot="content"
                            slot-scope="{ on }"
                        >
                            {{on ? "" : ""}}
                        </template>
                    </veui-switch>
                </veui-field>
                <veui-field label="">
                    <veui-switch
                        ui="m"
                        v-model="value2"
                    >
                        <template
                            slot="content"
                            slot-scope="{ on }"
                        >
                            <veui-icon :name="on ? 'mark-tick' : 'mark-x'"/>
                        </template>
                    </veui-switch>
                </veui-field>
            </veui-form>
        </section>

        <h2></h2>
        <section>
            <veui-form>
                <veui-field label="">
                    <veui-switch
                        ui="m"
                        :checked="false"
                        :loading="true"
                    >
                    </veui-switch>
                </veui-field>
                <veui-field label="">
                    <veui-switch
                        ui="m"
                        :checked="true"
                        :loading="true"
                    >
                    </veui-switch>
                </veui-field>
                <veui-field label="">
