                    </veui-switch>
                </veui-field>
                <veui-field label=" [ui=xs]">
                    <veui-switch
                        v-model="value1"
                        ui="xs"
                        :loading="loading"
                    >
                        
                    </veui-switch>
                    <veui-switch
                        v-model="value1"
                        ui="xs"
