                    />
                    <veui-switch
                        v-model="value1"
                        ui="xs"
                        disabled
                    />
                </veui-field>
                <veui-field label=" [ui=s]">
                    <veui-switch
                        v-model="value2"
                        :loading="loading"
                    >
                        
                    </veui-switch>
                    <veui-switch
