                        :loading="asyncLoading"
                        @click.prevent="toggle"
                    >
                        
                        <template
                            slot="content"
                            slot-scope="{ on }"
                        >
                            <veui-icon :name="on ? 'mark-tick' : 'mark-x'"/>
                        </template>
                    </veui-switch>
