<script>
import {Switch, Icon, Form, Field} from 'veui';
import 'veui-theme-blue-icons/mark-tick';
import 'veui-theme-blue-icons/mark-x';

export default {
    name: 'switch-demo',
    components: {
        'veui-switch': Switch,
        'veui-icon': Icon,
        'veui-form': Form,
        'veui-field': Field
    },
    data() {
        return {
            value1: true,
            value2: true,
            value3: true,
            loading: false,
            asyncValue: false,
            asyncLoading: false
        };
    },
    methods: {
        toggle() {
            this.asyncLoading = true;
            setTimeout(() => {
                this.asyncValue = !this.asyncValue;
                this.asyncLoading = false;
            }, 2000);
        }
    }
};
</script>

