import 'veui-theme-blue-icons/plain-left';
import 'veui-theme-blue-icons/plain-right';
import 'veui-theme-blue-icons/fast-left';
import 'veui-theme-blue-icons/fast-right';
import 'veui-theme-blue-icons/more-ellipsis';

import ui from 'veui/managers/ui';
import i18n from 'veui/managers/i18n';

i18n.register(
    'zh-Hans',
    {
        infoLabel: '{page}{pageCount}',
        pageSize: '{size} /',
        pageSizeLabel: '',
        total: '{total}',
        prev: '',
        next: '',
        pageLabel: '{page}',
        current: '{page}',
        gotoPage: '{page}',
        go: ''
    },
    {
        ns: 'pagination'
    }
);

ui.defaults(
    {
        icons: {
            prev: 'plain-left',
            next: 'plain-right',
            backward: 'fast-left',
            forward: 'fast-right',
            more: 'more-ellipsis'
        },
        ui: {
            size: {
                values: ['xs', 's', 'm'],
                inherit: true,
                default: 's'
            },
            style: {
                values: ['normal', 'number'],
                default: 'normal'
            },
        }
    },
    'pagination'
);
